.gallery{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h1{
        color: white;
        span{
            color: yellow;
        }
    }

    .pictures{
        width: 100%;
        height: 35vw;
        display: flex;
        align-items: center;
        justify-content: center;
        position: relative;
        margin: 4vw 0 5vw 0;
        overflow: hidden;

        .left_arrow{
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: start;
            cursor: pointer;
            
            
            h1{
                font-size: 10vw;
                font-weight: 100;
                margin: 0;
                z-index: 2;
            }
            
        }

        .back_left{
            position: absolute;
            z-index: 1;
            opacity: 0.5;
            display: flex;
            justify-content: start;
            left: 10vw;

            img{
                width: 50%;
                object-fit: contain;
            }
        }

        .front{
            flex: 10;
            display: flex;
            justify-content: center;
            align-items: center;
            

            img{
                width: 70%;
                object-fit: contain;
                z-index: 2;
            }
        }

        .back_right{
            position: absolute;
            z-index: 1;
            opacity: 0.7;
            display: flex;
            justify-content: end;
            right: 10vw;

            img{
                width: 50%;
                object-fit: contain;
            }
        }

        .right_arrow{
            flex: 1;
            display: flex;
            align-items: center;
            justify-content: end;
            cursor: pointer;
            

            h1{
                font-size: 10vw;
                font-weight: 100;
                margin: 0;
                z-index: 2;
            }
        }
    }
}