.perks{
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: stretch;

    .perk{
        flex: 4;
        padding: 3%;
        border-radius: 10%;
        background-color: #000000;
        -webkit-box-shadow: 0px 0px 20px 4px rgba(255,255,0,0.85); 
        box-shadow: 0px 0px 20px 4px rgba(255,255,0,0.85);
        margin: 3%;
        display: flex;
        flex-direction: column;

        .upper{
            flex:2;
            display: flex;
            justify-content: center;
            align-items: center;

            .left{
                flex: 7;
                overflow: hidden;
                padding: 3%;

                img{
                    width: 100%;
                    object-fit: contain;
                }
            }

            .right{
                flex: 5;
                padding: 3%;

                h3{
                    color: yellow;
                }
            }
        }
        .lower{
            flex: 10;
            padding: 3%;

            h4{
                margin: 0 0 5% 0;
                color: white;
            }

            p{
                color: white;
            }
        }
    }
}