.bottom{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 5vw;
    margin-top: 7vw;

    .left{
        width: 50%;
        margin-right: 2vw;
        
        h3{
            color: white;
            display: inline;
        }
        p{
            color: white;
            display: inline;
        }
    }

    .right{
        width: 50%;
        height: 300px;
        overflow: hidden;
        border-radius: 7%;
        -webkit-box-shadow: 0px 0px 20px 4px rgba(255,255,255,0.85); 
        box-shadow: 0px 0px 20px 4px rgba(255,255,255,0.85);
        margin-left: 2vw;
    }
}