.about{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 5vw;

    .info{
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;

        .left{
            width: 40%;
            display: flex;
            justify-content: center;
            align-items: center;

            img{
                width: 60%;
                object-fit: contain;
            }
        }

        .right{
            width: 60%;
            display: flex;
            justify-content: start;
            align-items: center;
            margin-right: 7vw;

            h3{
                color: white;
            }
        }
    }
}