.trainers{
    width: 90%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    h1{
        color: yellow;
        margin-top: 7vw;
        margin-bottom: 8vw;
    }

    .trainer1{
        width: 80%;
        display: flex;
        margin-bottom: 8vw;

        .left{
            width: 50%;
            display: flex;
            justify-content: center;

            .img_cont{
                width: 60%;
                display: flex;
                align-items: center;

                img{
                    width: 100%;
                    object-fit: contain;
                    -webkit-box-shadow: 0px 0px 20px 4px rgba(255,255,0,0.85); 
                    box-shadow: 0px 0px 20px 4px rgba(255,255,0,0.85);
                }
            }
            

            
        }

        .right{
            width: 50%;
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: start;

            h3{
                color: white;
            }

            p{
                color: white;
            }
        }
    }

    .trainer2{
        width: 80%;
