.feature_body{
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    -webkit-box-shadow: 0px 0px 20px 4px rgba(255,255,0,0.85); 
    box-shadow: 0px 0px 20px 4px rgba(255,255,0,0.85);
    margin: 12vw 0 7vw 0;

    h1{
        margin-top: 3vw;
        color: white;
    }

    .features{
        width: 100%;
        display: flex;
        flex-direction: row;
        justify-content: space-around;
        align-items: center;
        margin: 3vw 0 3vw 0;
        
        .feature{
            width: 20%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: center;
            
            .image{
                width: 85%;
                display: flex;
                justify-content: center;
                
                img{
                    width: 75%;
                    object-fit: contain;
                }
            }
            .text{

                h4{
                    color: white;
                    margin-top: 1vw;
                }
            }
        }
    }
}