.sideMenu{
    width: 30vw;
    height: 100vh;
    position: fixed;
    right: -30vw;
    top: 0;
    z-index: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: yellow;
    transition: all 2s ease;

    &.active{
        right: 0;
    }

    ul{
        margin: 0;
        padding: 0;
        list-style: none;
        font-size: 1.5vw;
        font-weight: 500;
        color: black;
        width: 60%;

        li{
            margin-bottom: 3vw;

            a{
                color: inherit;
                text-decoration: none;
            }

            &:hover{
                font-weight: 700;
            }
        }

    }
}