.top{
    width: 100%;

    .top_image{
        overflow: hidden;

        img{
            width: 100%;
            object-fit: contain;
        }
    }

    .free_div{
        width: 90%;
        position: absolute;
        top: 16vw;
        display: flex;
        justify-content: center;

        .left{
            flex: 6;
            display: flex;
            justify-content: start;

            h1{
                font-size: 3vw;
                color: white;
    
                span{
                    color: yellow;
                }
            }
        }

        .right{
            flex: 6;
            display: flex;
            justify-content: end;

            .icons{
                display: flex;
                flex-direction: column;
                justify-content: space-evenly;

                .icon{
                    color: white;
                    font-size: 3vw;
                }
            }
        }
    }
}