.top-bar{
    background-color: black;
    width: 100%;
    display: flex;
    justify-content: center;
    margin-top: 2vw;

    .left{
        flex: 6;
        display: flex;
        justify-content: start;

        h2{
            color: white;
        }
    }

    .right{
        flex: 6;
        display: flex;
        justify-content: end;
        align-items: center;

        .inner{
            height: 5vh;
            width: 5vh;
            display: flex;
            flex-direction: column;
            justify-content: space-around;
            z-index: 3;

            span{
                width: 5vh;
                background-color: white;
                height: 3px;
                transform-origin: left;
                transition: ease 2s;
            }
        }
    }

    &.active{
        
        .inner{

            #middle-line{
                opacity: 0;
                transition: none;
            }

            #top-line{
                rotate: 43deg;
                background-color: black;
            }

            #bottom-line{
                rotate: -43deg;
                background-color: black;
            }
        }
    }
}
